
import {
  defineComponent, PropType, ref,
} from 'vue';
import { useI18n } from 'vue-i18n';
import { DropdownItem } from '@/types/components';

export default defineComponent({
  props: {
    items: {
      type: Array as PropType<DropdownItem[]>,
      required: true,
    },
    selectedValue: {
      type: [String, Number],
    },
    position: {
      type: String,
      default: 'left',
      validators: (value: string) => ['left', 'right'].includes(value),
    },
    index: {
      type: Number,
    },
  },
  setup(props, ctx) {
    const { t } = useI18n();
    const show = ref(false);
    const listClass = {
      [`dropdown__list--${props.position}`]: true,
    };

    const toggle = () => {
      show.value = !show.value;
    };

    const onClick = (item: DropdownItem) => {
      if (item.disabled) return;

      ctx.emit('selected', {
        value: item.value,
        index: props.index,
      });
      toggle();
    };

    return {
      t,
      show,
      onClick,
      toggle,
      listClass,
    };
  },
});
