<template>
  <tool-tip :right="true">
    <template #element>
      <div class="daily-habit-state" :class="classes" @click="emitState"/>
    </template>
    <template #text>
      {{t('statuses.' + status)}}
    </template>
  </tool-tip>
</template>

<script lang="ts">
import {
  defineComponent, PropType, ref, watch,
} from 'vue';
import { useI18n } from 'vue-i18n';
import ToolTip from '@/components/ToolTip.vue';
import { Status } from '@/types/shared';

export default defineComponent({
  components: { ToolTip },
  props: {
    status: {
      type: String as PropType<Status>,
      default: 'open',
    },
  },
  setup(props, ctx) {
    const { t } = useI18n();
    const classes = ref([`daily-habit-state--${props.status}`]);

    watch(() => props.status, () => {
      classes.value = [`daily-habit-state--${props.status}`];
    });

    const emitState = () => {
      let status: Status;
      if (props.status === 'open' || props.status === 'progress') {
        status = 'done';
      } else if (props.status === 'done') {
        status = 'skip';
      } else {
        status = 'open';
      }

      ctx.emit('status', status);
    };

    return {
      t,
      classes,
      emitState,
    };
  },
});
</script>

<style lang="scss">
@import "@/assets/styles/utils/_functions";

$size: remSize(30px);
$lg-size: remSize(22px);

.daily-habit-state {
  @apply rounded-full border-gray-200 border-2 align-middle inline-block mx-0.5 text-white
  hover:border-gray-300;
  width: $size;
  height: $size;
  background-size: 70%;
  background-repeat: no-repeat;
  background-position: center;

  &--done {
    @apply bg-green-300 border-transparent hover:border-transparent;
    background-image: url('../../assets/images/icons/check_white.svg');
  }

  &--skip {
    @apply bg-blue-300 border-transparent hover:border-transparent;
    background-image: url('../../assets/images/icons/minus_white.svg');
  }
}
</style>
