<template>
  <div class="checkbox" :class="classes">
    <input
      class="checkbox__field"
      type="checkbox"
      v-bind="$attrs"
      @change="updateValue"
      :checked="isChecked"
    />
    <label class="checkbox__label" :for="$attrs.id"><slot /></label>
  </div>
</template>

<script lang="ts">
import {
  defineComponent, computed, watch, ref,
} from 'vue';

export default defineComponent({
  inheritAttrs: false,
  props: {
    modelValue: [Array, Boolean],
    invalid: Boolean,
  },
  emits: ['update:modelValue'],
  setup(props, ctx) {
    const invalid = ref(props.invalid);
    const classes = computed(() => ({
      'checkbox--error': invalid.value,
    }));

    watch(() => props.invalid, (newValue) => {
      invalid.value = newValue;
    });

    const isChecked = computed(() => {
      if (props.modelValue instanceof Array) {
        return props.modelValue.includes(ctx.attrs.value);
      }
      return props.modelValue;
    });

    const updateValue = (event: { target: { value: any; checked: boolean }; }) => {
      if (props.modelValue instanceof Array) {
        const newValue = [...props.modelValue];
        if (isChecked.value) {
          newValue.splice(newValue.indexOf(event.target.value), 1);
        } else {
          newValue.push(event.target.value);
        }
        ctx.emit('update:modelValue', newValue);
        return;
      }
      ctx.emit('update:modelValue', event.target.checked);
    };

    return {
      updateValue,
      isChecked,
      classes,
    };
  },
});
</script>

<style lang="scss">
@import '@/assets/styles/utils/_functions.scss';
$radio-dimension: 20px;

.checkbox {
  @apply inline-flex;
  &__field {
    appearance: none;
    @apply inline-block select-none border rounded-md
    border-gray-400 focus:ring-2 focus:ring-pink-200;
    width: remSize($radio-dimension);
    height: remSize($radio-dimension);
    flex: 0 0 remSize($radio-dimension);

    &:hover {
      @apply bg-gray-100;
    }

    &:checked {
      @apply bg-main-pink border-transparent;
      background-size: 90% 90%;
      background-image: url("../../assets/images/white-check.png");
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  &__label {
    @apply ml-2;
  }
}
</style>
