<template>
  <button type="button" class="icon-btn" :class="iconClass">
    <slot />
  </button>
</template>

<script lang="ts">
import { defineComponent, watch } from 'vue';

const iconClassName = {
  SELECTED: 'icon-btn--selected',
  FLAT: 'icon-btn--flat',
};

export default defineComponent({
  props: {
    flat: {
      type: Boolean,
      default: false,
    },
    selected: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    const iconClass = {
      [iconClassName.SELECTED]: props.selected,
      [iconClassName.FLAT]: props.flat,
    };

    watch(props, () => {
      iconClass[iconClassName.SELECTED] = props.selected;
      iconClass[iconClassName.FLAT] = props.flat;
    });

    return {
      iconClass,
    };
  },
});
</script>

<style lang="scss" scoped>
@import '@/assets/styles/utils/_functions.scss';
$size: 28px;

.icon-btn {
  @apply p-1 font-normal border border-gray-200 rounded-md;
  height: remSize($size);
  width: remSize($size);

  &--flat {
    @apply border-transparent;
  }

  &--selected {
    @apply bg-blue-100 border-transparent;
  }

  img {
    max-height: remSize($size);
  }
}
</style>
