<template>
  <button class="menu__button" @click="toggle">
    <img v-if="show" class="menu__icon" src="@/assets/images/menu-close.png" />
    <img v-else class="menu__icon" src="@/assets/images/menu-open.png" />
  </button>
</template>

<script>
import { defineComponent, ref } from 'vue';

export default defineComponent({
  props: {
    show: Boolean,
  },
  setup(props, ctx) {
    const toggle = () => {
      ctx.emit('updateShow', !props.show);
    };

    return {
      toggle,
    };
  },
});
</script>

<style lang="scss" scoped>
.menu {
  &__button {
    @apply fixed bottom-4 right-4 z-40;
  }

  &__icon {
    width: 50px;
    height: 50px;
  }
}
</style>
