<template>
  <li class="list-item" :class="classes">
    <slot />
  </li>
</template>

<script lang="ts">
import { defineComponent, computed } from 'vue';

export default defineComponent({
  props: {
    isTitle: Boolean,
  },
  setup(props) {
    const classes = computed(() => ({
      'list-item--title': !!props.isTitle,
    }));

    return {
      classes,
    };
  },
});
</script>

<style lang="scss">
.list-item {
    @apply p-4;

    &:not(.list-item--title) {
      @apply hover:bg-gray-50;
    }

    &--title {
      @apply font-semibold text-lg;
    }
}
</style>
