
import { defineComponent, ref, watch } from 'vue';

export default defineComponent({
  props: {
    selected: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    const classes = ref<Record<string, boolean>>({
      'rounded-item--selected': props.selected,
    });

    watch(() => props.selected, (newValue) => {
      classes.value['rounded-item--selected'] = newValue;
    });

    return {
      classes,
    };
  },
});
