<template>
  <div class="select">
    <label class="select__label">{{label}}</label>
    <select
      class="select__field"
      :class="classes"
      v-bind="$attrs"
      :value="modelValue"
      @input="$emit('update:modelValue', $event.target.value)"
      >
        <slot />
    </select>
    <label v-if="invalid" class="select__error">{{error}}</label>
  </div>
</template>

<script lang="ts">
import {
  defineComponent, computed, watch, ref,
} from 'vue';

export default defineComponent({
  inheritAttrs: false,
  props: {
    modelValue: [String, Number],
    label: {
      type: String,
    },
    error: {
      type: String,
    },
    invalid: Boolean,
  },
  emits: ['update:modelValue'],
  setup(props) {
    const invalid = ref(props.invalid);
    const classes = computed(() => ({
      'select--error': invalid.value,
    }));

    watch(() => props.invalid, (newValue) => {
      invalid.value = newValue;
    });

    return {
      classes,
    };
  },
});
</script>

<style lang="scss">
.select {
  @apply relative my-4;

  &--error {
    @apply border-red-500;
  }

  &__field {
    @apply border p-2 focus:ring-main-pink focus:ring-1 focus:outline-none;;
    width: 100%;
  }

  &__label:not(:empty) {
    @apply absolute text-sm bg-white top-0 left-6 -m-2.5 px-1;
  }

  &__error {
    @apply text-red-500 text-sm;
  }
}
</style>
