<template>
  <app-header>
    <template #additional>
      <select-option v-if="validOptionRoute" />
    </template>
  </app-header>
  <div class="main">
    <div class="side-bar" :class="sideBarClass">
      <div class="side-bar__content">
        <links />
      </div>
    </div>
    <div class="main-content">
      <!-- <router-view v-slot="{ Component }">
        <transition>
          <keep-alive>
            <component :is="Component" />
          </keep-alive>
        </transition>
      </router-view> -->
      <router-view />
    </div>
  </div>
  <menu-icon class="menu-icon" :show="show" @updateShow="shouldShow"/>
</template>

<script lang="ts">
import {
  defineComponent, ref, computed, watch,
} from 'vue';
import { useRoute } from 'vue-router';
import AppHeader from '@/components/Header.vue';
import SelectOption from '@/components/AddItem/SelectOption.vue';
import Links from '@/components/Links.vue';
import MenuIcon from '@/components/MenuIcon.vue';
import AppService from './services/appService';
import { dayMap } from '@/types/shared';

const service = new AppService();

export default defineComponent({
  components: {
    AppHeader,
    SelectOption,
    MenuIcon,
    Links,
  },
  setup() {
    const currentDay = dayMap[(new Date()).getDay()];
    service.checkAndDelete(currentDay)
      .then()
      .catch((res) => console.log(res));
    const show = ref(false);
    const route = useRoute();
    const shouldShow = (value: boolean) => {
      show.value = value;
    };
    const validOptionRoute = ref(!['Create', 'Habits'].includes(route.name as string));
    const sideBarClass = computed(() => ({
      'side-bar--open': show.value,
    }));

    watch(() => route.path, () => {
      validOptionRoute.value = !['Create', 'Habits'].includes(route.name as string);
      show.value = false;
    });

    return {
      show,
      shouldShow,
      sideBarClass,
      validOptionRoute,
    };
  },
});
</script>

