/* eslint-disable class-methods-use-this */
import {
  ToDoList,
  ErrorCode,
  AppError, ArchiveType, Archive, HabitDetail,
} from '@/types/shared';
import storage from './storage';
import { EmptyObject, STORAGE_KEYS } from './utils/helpers';

const localStorage = storage.local;

export interface ArchiveServiceInt {
  fetchArchives(type: ArchiveType): Promise<Archive | AppError>,
  addToArchive(value: HabitDetail | ToDoList, type: ArchiveType): Promise<EmptyObject | AppError>,
  deleteArchivePermanently(id: string | number, type: ArchiveType): Promise<null | AppError>
  clearArchive(type: ArchiveType): Promise<null | AppError>
}

export default class ArchiveService implements ArchiveServiceInt {
  addToArchive(value: HabitDetail | ToDoList, type: ArchiveType): Promise<AppError | EmptyObject> {
    try {
      let archives = localStorage.get(STORAGE_KEYS.archive) as Archive;

      if (!archives) {
        archives = {
          habit: [],
          todoList: [],
        };
      }

      archives[type].push(value as any);
      localStorage.set(STORAGE_KEYS.archive, archives);

      return Promise.resolve(value as EmptyObject);
    } catch {
      return Promise.resolve(new AppError({
        type: 'archive',
        code: ErrorCode.InsertFailed,
      }));
    }
  }

  deleteArchivePermanently(id: string | number, type: ArchiveType): Promise<AppError | null> {
    try {
      const archives = localStorage.get(STORAGE_KEYS.archive) as Archive;

      if (!(archives[type] as any[])
        .find((archive: { id: string | number; }) => archive.id === id)) {
        throw new Error();
      }

      const newValue = (archives[type] as any[])
        .filter((archive: { id: string | number; }) => archive.id !== id);
      archives[type] = newValue;

      localStorage.set(STORAGE_KEYS.archive, archives);

      return Promise.resolve(null);
    } catch {
      return Promise.resolve(new AppError({
        type: 'archive',
        code: ErrorCode.RemoveFailed,
      }));
    }
  }

  clearArchive(type: ArchiveType): Promise<AppError | null> {
    try {
      const archives = localStorage.get(STORAGE_KEYS.archive) as Archive;
      archives[type] = [];
      localStorage.set(STORAGE_KEYS.archive, archives);
      return Promise.resolve(null);
    } catch {
      return Promise.resolve(new AppError({
        type: 'archive',
        code: ErrorCode.RemoveAllFailed,
      }));
    }
  }

  fetchArchives(): Promise<AppError | Archive> {
    try {
      const archives = localStorage.get(STORAGE_KEYS.archive) as Archive;

      if (archives) {
        return Promise.resolve(archives);
      }

      return Promise.resolve(new AppError({
        type: 'archive',
        code: ErrorCode.NotFound,
      }));
    } catch {
      return Promise.resolve(new AppError({
        type: 'archive',
        code: ErrorCode.FetchAllFailed,
      }));
    }
  }
}
