<template>
  <modal @close="$emit('close')" class="add-item">
    <template #title>
      {{title}}
    </template>
    <template #body>
      <form novalidate @submit.prevent="submitForm" id="addItemForm">
        <input-field
          v-model="formData.description"
          type="text"
          :label="t('addItemComponent.labels.messageLabel')"
          :error="validationObject.message.error"
          :invalid="!validationObject.message.valid"
          @focus="onFocusClearValidation"
          maxlength="250"
          name="message"
          id="addItemForm-message"
          required
        />
        <div class="add-item__checkbox">
          <checkbox id="addItemForm-addTime" v-model="shouldAddTime">
            {{ t("addItemComponent.labels.addTimeLabel") }}
          </checkbox>
        </div>
        <div class="form-group" v-if="shouldAddTime">
          <input-field
            v-model="formData.startTime"
            type="time"
            :error="validationObject.timeFrom.error"
            :invalid="!validationObject.timeFrom.valid"
            @focus="onFocusClearValidation"
            :label="t('addItemComponent.labels.startTimeLabel')"
            name="timeFrom"
            id="addItemForm-timeFrom"
            required
          />
          <input-field
            :disabled="!formData.startTime"
            v-model="formData.endTime"
            :min="formData.startTime"
            type="time"
            :error="validationObject.timeTo.error"
            :invalid="!validationObject.timeTo.valid"
            @focus="onFocusClearValidation"
            :label="t('addItemComponent.labels.endTimeLabel')"
            name="timeTo"
            id="addItemForm-timeTo"
            required
          />
        </div>
        <div class="add-item__checkbox" v-if="!isHabit">
          <checkbox id="addItemForm-carryOver" v-model="formData.carryOver">
            {{ t("addItemComponent.labels.carryOverLabel") }}
          </checkbox>
        </div>
        <div class="add-item__checkbox" v-if="!isHabit">
          <checkbox id="addItemForm-optional" v-model="formData.optional">
            {{ t("addItemComponent.labels.optional") }}
          </checkbox>
        </div>
        <template v-if="isHabit">
          <round-item-selection
            :label="t('addItemComponent.labels.selectDaysLabel')"
            :error="validationObject.selectedDays.error"
            :invalid="!validationObject.selectedDays.valid"
            @click="onFocusClearValidation('selectedDays')"
          >
            <round-item
              v-for="(customDay, index) in customDays"
              :key="customDay.key"
              @click="selectDay(index, customDay)"
              :selected="customDay.selected"
            >
              {{ customDay.translation }}
            </round-item>
          </round-item-selection>
          <div class="add-item__checkbox">
            <checkbox id="addItemForm-endDate" v-model="shouldEnd">
              {{ t("addItemComponent.labels.endDateLabel") }}
            </checkbox>
          </div>
          <input-field
            v-model="formData.endDate"
            v-if="shouldEnd"
            type="date"
            :min="dateNow"
            :error="validationObject.endsOn.error"
            :invalid="!validationObject.endsOn.valid"
            :label="t('addItemComponent.labels.selectDateLabel')"
            name="endsOn"
            id="addItemForm-endsOn"
            required
          />
          <select-field
            v-model="formData.categoryId"
            :label="t('categories.categories')"
          >
            <option
              v-for="category in categories"
              :key="category.id"
              :value="category.id"
            >
            {{t(`categories.${category.name}`)}}
            </option>
          </select-field>
        </template>
        <div class="add-item__checkbox" v-if="isHabit">
          <checkbox id="addItemForm-endDate" v-model="formData.save">
            {{ t("addItemComponent.labels.save") }}
          </checkbox>
        </div>
        <alert v-if="hasError" variant="error" @close="hasError = false">
          <template #title>Server error</template>
          <template #content>Could not add an item to the list.</template>
        </alert>
      </form>
    </template>
    <template #footer>
      <button-group>
        <app-button @click="$emit('close')" @keydown="$emit('close')">{{
          t("addItemComponent.cancelBtn")
        }}</app-button>
        <app-button variant="primary" form="addItemForm">{{ submitBtnText }}</app-button>
      </button-group>
    </template>
  </modal>
</template>

<script lang="ts">
import {
  defineComponent, PropType, reactive, ref,
} from 'vue';
import { useI18n } from 'vue-i18n';
import { cloneDeep } from 'lodash';
import { useStore } from 'vuex';
import AppButton from '@/components/buttons/AppButton.vue';
import InputField from '@/components/forms/InputField.vue';
import {
  ToDoItem, Day, ToDoItemForm, ListDayType, dayMap,
} from '@/types/shared';
import { CreateMode } from '@/services/utils/link';
import RoundItem from '@/components/RoundItem.vue';
import { getDateString } from '@/services/utils/time';
import Checkbox from '@/components/forms/Checkbox.vue';
import Modal from '@/components/Modal.vue';
import ButtonGroup from '../ButtonGroup.vue';
import RoundItemSelection from '../RoundItemSelection.vue';
import validation from './features/validation';
import Alert from '../Alert.vue';
import { createToDoItemReq, createHabitItemReq } from './features/helpers';
import SelectField from '../forms/SelectField.vue';
import categories from '@/types/categories';

interface CustomDay {
  key: string,
  translation: string
  selected: boolean
}

// todo : save habit to archive

export default defineComponent({
  props: {
    todoItem: {
      type: Object as PropType<ToDoItem & { endDate?: number }>,
      default: () => ({}),
    },
    listType: {
      type: String as PropType<ListDayType>,
      default: null,
    },
    habit: {
      type: Boolean,
      default: false,
    },
    mode: {
      type: String as PropType<CreateMode>,
      default: 'create',
    },
  },
  components: {
    AppButton,
    InputField,
    RoundItem,
    Checkbox,
    Modal,
    ButtonGroup,
    RoundItemSelection,
    Alert,
    SelectField,
  },
  setup(props, ctx) {
    const store = useStore();
    const { t } = useI18n();
    const isHabit = props.habit || props.todoItem.habit;
    const isEditMode = props.mode === 'edit';
    const submitBtnText = t(`addItemComponent.${isEditMode ? 'edit' : 'create'}Btn`);
    const shouldEnd = ref(!!props.todoItem.endDate);
    const shouldAddTime = ref(!!props.todoItem.startTime);
    const dateNow = getDateString(Date.now());
    const title = t(`addItemComponent.heading.${isEditMode ? 'edit' : 'create'}`);
    const hasError = ref(false);
    const formData = ref<ToDoItemForm>({
      id: props.todoItem.id,
      key: props.todoItem.key || '',
      description: props.todoItem.description || '',
      startTime: props.todoItem.startTime || '',
      carryOver: props.todoItem.carryOver || false,
      days: props.todoItem.days || [],
      endTime: props.todoItem.endTime || '',
      status: props.todoItem.status || 'open',
      habit: isHabit,
      optional: props.todoItem.optional || false,
      endDate: getDateString(props.todoItem.endDate || 0),
      save: props.todoItem.save || false,
      categoryId: props.todoItem.categoryId || undefined,
    });

    const validationObject = ref(validation(t, true));
    const onFocusClearValidation = (e: any | string) => {
      if (typeof e === 'string') {
        validationObject.value[e].valid = true;
        return;
      }

      validationObject.value[e.target.name].valid = true;
    };

    const customDaysDefault = [...Array(7).keys()].map((key: number) => ({
      key: dayMap[key],
      translation: t(`days.abbr.${dayMap[key]}`),
      selected: formData.value.days?.includes(dayMap[key]) || false,
    }));
    const customDays = reactive(customDaysDefault);

    const selectDay = (index: number) => {
      customDays[index].selected = !customDays[index].selected;
    };

    const setErrorState = (value: boolean) => {
      hasError.value = value;
    };

    const submitForm = async (e: any) => {
      setErrorState(false);
      formData.value.days = Object.values(customDays)
        .filter((customDay: CustomDay) => customDay.selected)
        .map((customDay: CustomDay) => customDay.key as Day);

      if (!shouldAddTime.value) {
        formData.value.startTime = '';
        formData.value.endTime = '';
      }

      const validDaySelection = isHabit === false || formData.value.days.length > 0;

      if (!e.target.checkValidity() || (isHabit && formData.value.days.length === 0)) {
        validationObject.value = (validation(t, validDaySelection));
        return;
      }

      // check if the user is adding a new todo item
      // the new item will not be save automatically
      if (!isEditMode && !isHabit) {
        ctx.emit('addItem', cloneDeep(formData.value));
        ctx.emit('close');
        return;
      }

      let requestData;
      let storeAction: string;
      if (isHabit) {
        requestData = createHabitItemReq(cloneDeep(formData.value));
        storeAction = isEditMode ? 'habits/update' : 'habits/add';
      } else {
        requestData = {
          listType: props.listType,
          item: createToDoItemReq(cloneDeep(formData.value)),
        };
        storeAction = 'todos/update';
      }

      const result = await store.dispatch(storeAction, requestData);
      console.log(result);

      ctx.emit('itemSaved', cloneDeep(formData.value));
      ctx.emit('close');
    };

    return {
      t,
      submitBtnText,
      customDays,
      selectDay,
      submitForm,
      shouldEnd,
      dateNow,
      isHabit,
      title,
      formData,
      shouldAddTime,
      validationObject,
      onFocusClearValidation,
      hasError,
      categories,
    };
  },
});
</script>

<style lang="scss" scoped>
@import "@/assets/styles/index.scss";

.add-item {
  .form-group {
    @apply flex justify-between;

    > * {
      @apply flex-1;

      &:not(:last-child) {
        @apply mr-3;
      }
    }
  }

  &__checkbox {
    @apply py-2;
  }
}
</style>
