<template>
  <div class="rounded-item" :class="classes">
      <slot />
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, watch } from 'vue';

export default defineComponent({
  props: {
    selected: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    const classes = ref<Record<string, boolean>>({
      'rounded-item--selected': props.selected,
    });

    watch(() => props.selected, (newValue) => {
      classes.value['rounded-item--selected'] = newValue;
    });

    return {
      classes,
    };
  },
});
</script>

<style lang="scss" scoped>
@import '@/assets/styles/utils/_functions.scss';

.rounded-item {
  @apply bg-gray-400 p-3 flex items-center justify-center font-semibold overflow-hidden
  text-white rounded-full cursor-default;
  width: remSize(30px);
  height: remSize(30px);

  &--selected {
    @apply bg-main-pink;
  }
}
</style>
