import { ActionTree, MutationTree } from 'vuex';
import {
  Habit, Archive, AppError, ArchiveType,
} from '@/types/shared';
import { RootState } from './types';
import ToDoService from '@/services/archiveService';

const service = new ToDoService();

const archiveState = () => ({
  archive: {} as Archive | Record<string, unknown>,
});

export type ArchiveState = ReturnType<typeof archiveState>;

export const mutations: MutationTree<ArchiveState> = {
  UPDATE(state, payload: Archive | Record<string, unknown>) {
    state.archive = payload;
  },
};

export const actions: ActionTree<ArchiveState, RootState> = {
  async fetch({ commit }) {
    const result: Archive | AppError = await service.fetchArchives();

    if (!(result instanceof AppError)) {
      commit('UPDATE', result);
    }

    return result;
  },
  async delete({ dispatch },
    { id, type }: {id: string, type: ArchiveType}) {
    const result: null | AppError = await service.deleteArchivePermanently(id, type);

    if (!(result instanceof AppError)) {
      const lastResult = await dispatch('fetch');
      return lastResult;
    }

    return result;
  },
  async deleteAll({ dispatch }, type: ArchiveType) {
    const result: null | AppError = await service.clearArchive(type);

    if (!(result instanceof AppError)) {
      const lastResult = await dispatch('fetch');
      return lastResult;
    }

    return result;
  },
};

export default {
  namespaced: true,
  state: archiveState,
  mutations,
  actions,
};
