
import { defineComponent } from 'vue';

export default defineComponent({
  props: {
    variant: {
      type: String,
      validator: (value: string) => ['green', 'yellow', 'purple', 'red', 'blue', 'indigo'].includes(value),
    },
  },
  setup(props) {
    const classes = {
      [`tag--${props.variant}`]: !!props.variant,
    };

    return {
      classes,
    };
  },
});
