const methods = (storageKey: string) => ({
  storage() {
    if (storageKey === 'local') {
      return window.localStorage;
    }
    return window.sessionStorage;
  },
  set(key: string, value: unknown) : boolean {
    this.storage().setItem(key, btoa(JSON.stringify(value)));
    return true;
  },
  get(key: string) : unknown {
    const value = this.storage().getItem(key) as string;
    return !value ? value : JSON.parse(atob(value));
  },
  remove(key: string): boolean {
    this.storage().removeItem(key);
    return true;
  },
});

export type WebStorage = ReturnType<typeof methods>;

export default {
  session: methods('session'),
  local: methods('local'),
};
