<template>
    <button v-if="!link" class="btn" :class="classes" v-bind="$attrs">
      <spinner-icon v-show="loading" class="btn__spinner"/>
      <span class="btn__content">
        <slot/>
      </span>
    </button>
    <a v-if="link" class="btn" :class="classes" v-bind="$attrs">
      <spinner-icon v-show="loading" class="btn__spinner"/>
      <span class="btn__content">
        <slot/>
      </span>
    </a>
</template>

<script lang="ts">
import { defineComponent, PropType, watch } from 'vue';
import SpinnerIcon from '../spinner/SpinnerIcon.vue';

type Variants = 'secondary' | 'primary' | 'flat';

export default defineComponent({
  components: { SpinnerIcon },
  inheritAttrs: false,
  props: {
    variant: {
      type: String as PropType<Variants>,
      default: 'secondary',
    },
    link: {
      type: Boolean,
      default: false,
    },
    loading: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    const classes = {
      [`btn--${props.variant}`]: true,
      'btn--loading': props.loading,
    };

    watch(props, () => {
      classes['btn--loading'] = props.loading;
    });

    return {
      classes,
    };
  },
});
</script>

<style lang="scss" scoped>
@import "./button";
</style>
