
import { defineComponent, PropType, watch } from 'vue';
import SpinnerIcon from '../spinner/SpinnerIcon.vue';

type Variants = 'secondary' | 'primary' | 'flat';

export default defineComponent({
  components: { SpinnerIcon },
  inheritAttrs: false,
  props: {
    variant: {
      type: String as PropType<Variants>,
      default: 'secondary',
    },
    link: {
      type: Boolean,
      default: false,
    },
    loading: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    const classes = {
      [`btn--${props.variant}`]: true,
      'btn--loading': props.loading,
    };

    watch(props, () => {
      classes['btn--loading'] = props.loading;
    });

    return {
      classes,
    };
  },
});
