export type ValidationObject = Record<string, {valid: boolean, error: string}>

type TranslationFn<T> = (key: string | number) => T;

export default (t: TranslationFn<string>, validDaySelection: boolean): ValidationObject => {
  const validationObject:ValidationObject = {
    message: {
      valid: true,
      error: '',
    },
    timeFrom: {
      valid: true,
      error: '',
    },
    timeTo: {
      valid: true,
      error: '',
    },
    selectedDays: {
      valid: validDaySelection,
      error: t('addItemComponent.errors.daysRequired'),
    },
    endsOn: {
      valid: true,
      error: '',
    },
  };

  const inputs = document.querySelectorAll('input[id*=addItemForm-]');
  inputs.forEach((input: any) => {
    if (input.type === 'checkbox') {
      return;
    }

    if (input.validity.rangeUnderflow) {
      validationObject[input.name as string].valid = false;
      validationObject[input.name as string].error = t(`addItemComponent.errors.${input.name}.rangeUnderflow`);
      return;
    }

    if (input.validity.valueMissing) {
      validationObject[input.name as string].valid = false;
      validationObject[input.name as string].error = t('addItemComponent.errors.valueMissing');
      return;
    }

    if (!input.validity.valid) {
      validationObject[input.name as string].valid = false;
      validationObject[input.name as string].error = t('addItemComponent.errors.invalid');
    }
  });

  return validationObject;
};
