/* eslint-disable import/prefer-default-export */
import { v4 as uuid } from 'uuid';
import { Habit, ToDoItem, ToDoItemForm } from '@/types/shared';

export const createToDoItemReq = (formData: ToDoItemForm): ToDoItem => ({
  id: formData.id || uuid(),
  key: formData.key || '',
  description: formData.description || '',
  startTime: formData.startTime || '',
  carryOver: formData.carryOver || false,
  days: formData.days || [],
  endTime: formData.endTime || '',
  status: formData.status || 'open',
  habit: formData.habit,
  optional: formData.optional || false,
});

export const createHabitItemReq = (formData: ToDoItemForm): Habit => ({
  id: formData.id,
  startTime: formData.startTime,
  endTime: formData.endTime,
  endDate: formData.endDate ? new Date(formData.endDate).getTime() : 0,
  days: formData.days || [],
  description: formData.description,
  save: formData.save || false,
  categoryId: formData.categoryId,
});
