import {
  Habit, AppError, HabitCount, Day, ToDoList, Status, ToDoItem,
} from '@/types/shared';

export default abstract class AbstractHabitService {
    abstract getHabit(id: string | number): Promise<Habit | AppError>;

    abstract getHabits(): Promise<Habit[] | AppError>;

    abstract updateHabit(habit: Habit): Promise<Habit | AppError>

    abstract getHabitCount(id: string | number, year?: number | null, desc?: boolean)
    : Promise<HabitCount[] | AppError>;

    abstract createDailyHabitTodos(day: Day): Promise<ToDoList | AppError>;

    abstract removeHabit(id: string | number, force?: boolean): Promise<void | AppError>

    abstract addHabit(todo: Habit): Promise<Habit | AppError>;

    abstract addHabitCount(count: HabitCount): Promise<HabitCount | AppError>;

    abstract addHabitCounts(list: ToDoList): Promise<HabitCount[] | AppError>;

    abstract updateStatus(status: Status, id: string | number): Promise<ToDoItem | AppError>;

    abstract removeTodo(id: string | number): Promise<void>

    abstract removeExpiredHabits(): Promise<void>
}
