
import { defineComponent, PropType } from 'vue';

export type AlertVariant = 'warning' | 'info' | 'success' | 'error';

export default defineComponent({
  props: {
    variant: {
      type: String as PropType<AlertVariant>,
      default: 'info',
    },
    closeable: {
      type: Boolean,
      default: true,
    },
  },
  setup(props) {
    const classes = {
      [`alert--${props.variant}`]: true,
    };

    return {
      classes,
    };
  },
});
