<template>
  <div class="tag" :class="classes">
    <span>
      <slot />
    </span>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue';

export default defineComponent({
  props: {
    variant: {
      type: String,
      validator: (value: string) => ['green', 'yellow', 'purple', 'red', 'blue', 'indigo'].includes(value),
    },
  },
  setup(props) {
    const classes = {
      [`tag--${props.variant}`]: !!props.variant,
    };

    return {
      classes,
    };
  },
});
</script>

<style lang="scss" scoped>
.tag {
  @apply inline-block py-0.5 px-1.5 mx-0.5 bg-gray-400 text-white text-sm rounded-2xl;

  &--blue {
    @apply bg-blue-400 text-white;
  }

  &--green {
    @apply bg-green-400 text-white;
  }

  &--yellow {
    @apply bg-yellow-400 text-white;
  }

  &--purple {
    @apply bg-purple-400 text-white;
  }

  &--red {
    @apply bg-red-400 text-white;
  }

  &--indigo {
    @apply bg-indigo-400 text-white;
  }
}
</style>
