<template>
  <div class="todo" :class="classes">
    <div class="todo__el" v-if="editMode">
      <icon-button
        @click="show = true">
        <img src="@/assets/images/icons/edit.svg" alt="edit">
      </icon-button>
    </div>
    <div class="todo__el" v-if="viewMode">
      <tool-tip>
        <template #element>
          <button @click="setStatus" class="todo__el todo__done-btn">
            <img
              v-if="isDone"
              src="@/assets/images/idea-highlighted.svg"
              alt="done"
              class="todo__img"
            />
            <img
              v-else
              src="@/assets/images/idea.svg"
              alt="open"
              class="todo__img  todo__el"
            />
          </button>
        </template>
        <template #text>
          {{markText}}
        </template>
      </tool-tip>
    </div>
    <div class="todo__content todo__el">
      <p class="todo__description">{{todo.description}}</p>
      <template v-if="todo.startTime">
        <span class="todo__time">
          <!--eslint-disable-next-line max-len -->
          <time :datetime="todo.startTime" class="time-block">{{todo.startTime}}</time> -
          <time :datetime="todo.startTime" class="time-block">{{todo.endTime}}</time>
        </span>
        <br />
      </template>
      <tag v-if="todo.status === 'done' && editMode" variant="green">
        {{t('status')}}: {{t('todos.optional')}}
      </tag>
      <tag v-if="todo.optional">{{t('todos.optional')}}</tag>
      <tag v-if="todo.carryOver" variant="purple">{{t('todos.carryOver')}}</tag>
    </div>
    <div v-if="editMode" class="todo__el todo__checkbox">
      <checkbox v-model="isChecked" />
    </div>
    <add-item
      v-if="show"
      @close="show = false"
      @itemSaved="itemSaved"
      mode="edit"
      :listType="listType"
      :todoItem="todoItem"
    />
  </div>
</template>

<script lang="ts">
import {
  defineComponent, PropType, ref, watch,
} from 'vue';
import { cloneDeep } from 'lodash';
import { useI18n } from 'vue-i18n';
import { ListDayType, ToDoItemExtension } from '@/types/shared';
import Checkbox from '@/components/forms/Checkbox.vue';
import IconButton from '@/components/IconButton.vue';
import AddItem from '@/components/AddItem/AddItem.vue';
import Tag from '@/components/Tag.vue';
import ToolTip from '@/components/ToolTip.vue';

export default defineComponent({
  components: {
    Checkbox,
    IconButton,
    AddItem,
    Tag,
    ToolTip,
  },
  props: {
    isArchive: {
      type: Boolean,
      default: false,
    },
    todo: {
      type: Object as PropType<ToDoItemExtension>,
      required: true,
    },
    index: {
      type: Number,
      default: -1,
    },
    isEditMode: {
      type: Boolean,
      default: false,
    },
    isForToday: {
      type: Boolean,
      default: true,
    },
    listType: {
      type: String as PropType<ListDayType>,
      default: null,
    },
    checked: Boolean,
  },
  setup(props, ctx) {
    const { t } = useI18n();
    const isChecked = ref(props.todo.checked);
    const show = ref(false);
    const isDone = ref<boolean>(props.todo.status === 'done');
    const classes = ref({
      'todo--done': isDone.value,
    });
    const todoItem = ref<ToDoItemExtension>(cloneDeep(props.todo));
    const markText = ref(t(isDone.value ? 'todos.mark' : 'todos.unmark'));
    const editMode = props.isEditMode && !props.isArchive;
    const viewMode = !props.isEditMode && props.isForToday && !props.isArchive;

    watch(isChecked, (newValue) => {
      ctx.emit('setChecked', {
        checked: newValue,
        index: props.index,
      });
    });

    watch(() => props.todo, (newValue) => {
      isChecked.value = newValue.checked;
    });

    const itemSaved = () => {
      ctx.emit('itemSaved');
    };

    watch(() => props.todo, (newValue) => {
      todoItem.value = cloneDeep(props.todo);
      isDone.value = newValue.status === 'done';
      classes.value = {
        'todo--done': isDone.value,
      };
      markText.value = t(isDone.value ? 'todos.mark' : 'todos.unmark');
    });

    const setStatus = () => {
      const status = props.todo.status === 'done' ? 'open' : 'done';
      ctx.emit('statusUpdate', {
        id: props.todo.id,
        status,
      });
    };

    return {
      t,
      show,
      isChecked,
      isDone,
      todoItem,
      classes,
      setStatus,
      itemSaved,
      markText,
      editMode,
      viewMode,
    };
  },
});
</script>

<style lang="scss" scoped>
@import '@/assets/styles/utils/_functions.scss';

.todo {
  @apply flex justify-between;

  &__content {
    @apply flex-1;
  }

  &--done {
    .todo__description {
      @apply text-green-500;
    }
  }

  &__done-btn {
    width: remSize(35px);
    flex: 0 0 remSize(35px);
  }

  &__el {
    &:not(:last-child) {
      @apply mr-3;
    }
  }

  &__checkbox {
    @apply mt-1;
  }
}

.todo:deep(.tag) {
  @apply mt-1;
}
</style>
