<template>
  <div class="dropdown">
    <button class="dropdown__expand focus:ring-main-pink focus:ring-opacity-10 focus:ring">
      <img src="@/assets/images/expand.svg" @click="toggle" alt="expand" />
    </button>
    <ul class="dropdown__list" :class="listClass" v-show="show">
      <li class="dropdown__item dropdown__item--label" @click="show = false">
        {{t('dropdown.close')}}
      </li>
      <li
        v-for="item in items"
        :key="item.value"
        @click="onClick(item)"
        class="dropdown__item"
        :class="{
          'dropdown__item--selected': selectedValue === item.value,
          'dropdown__item--disabled': item.disabled,
        }"
      >
        {{item.name}}
      </li>
    </ul>
  </div>
</template>

<script lang="ts">
import {
  defineComponent, PropType, ref,
} from 'vue';
import { useI18n } from 'vue-i18n';
import { DropdownItem } from '@/types/components';

export default defineComponent({
  props: {
    items: {
      type: Array as PropType<DropdownItem[]>,
      required: true,
    },
    selectedValue: {
      type: [String, Number],
    },
    position: {
      type: String,
      default: 'left',
      validators: (value: string) => ['left', 'right'].includes(value),
    },
    index: {
      type: Number,
    },
  },
  setup(props, ctx) {
    const { t } = useI18n();
    const show = ref(false);
    const listClass = {
      [`dropdown__list--${props.position}`]: true,
    };

    const toggle = () => {
      show.value = !show.value;
    };

    const onClick = (item: DropdownItem) => {
      if (item.disabled) return;

      ctx.emit('selected', {
        value: item.value,
        index: props.index,
      });
      toggle();
    };

    return {
      t,
      show,
      onClick,
      toggle,
      listClass,
    };
  },
});
</script>

<style lang="scss" scoped>
@import '@/assets/styles/utils/_functions.scss';
.dropdown {
  @apply relative text-base font-normal;

  &__expand {
    height: remSize(18px);

    img {
      height: remSize(18px);
    }
  }

  &__list {
    @apply absolute bg-white shadow-lg top-5;
    min-width: remSize(150px);

    &--right {
      @apply right-0;
    }

    &--left {
      @apply left-0;
    }
  }

  &__item {
    @apply p-3 hover:bg-gray-50 cursor-default;

    &--selected {
      @apply bg-green-100 hover:bg-transparent;
    }

    &--disabled {
      @apply text-gray-500 hover:bg-transparent;
    }

    &--label {
      @apply text-sm font-semibold py-2 text-main-pink hover:bg-transparent;
    }
  }
}
</style>
