
import {
  defineComponent, PropType, ref, watch,
} from 'vue';
import { useI18n } from 'vue-i18n';
import ToolTip from '@/components/ToolTip.vue';
import { Status } from '@/types/shared';

export default defineComponent({
  components: { ToolTip },
  props: {
    status: {
      type: String as PropType<Status>,
      default: 'open',
    },
  },
  setup(props, ctx) {
    const { t } = useI18n();
    const classes = ref([`daily-habit-state--${props.status}`]);

    watch(() => props.status, () => {
      classes.value = [`daily-habit-state--${props.status}`];
    });

    const emitState = () => {
      let status: Status;
      if (props.status === 'open' || props.status === 'progress') {
        status = 'done';
      } else if (props.status === 'done') {
        status = 'skip';
      } else {
        status = 'open';
      }

      ctx.emit('status', status);
    };

    return {
      t,
      classes,
      emitState,
    };
  },
});
