<template>
  <card class="daily-habit-item">
    <template #body>
      <div class="daily-habit-item__content">
        <div class="daily-habit-item__description">
          {{todo.description}}
          <p v-if="todo.startTime && todo.endTime" class="daily-habit-item__time-block">
            <time :datetime="todo.startTime" class="time-block">{{todo.startTime}}</time> -
            <time :datetime="todo.startTime" class="time-block">{{todo.endTime}}</time>
          </p>
          <p class="daily-habit-item__status">
            <strong>{{t('status')}}:</strong>
            {{t('statuses.' + todo.status)}}
          </p>
        </div>
        <div class="daily-habit-item__status">
          <habit-status
            :status="todo.status"
            @status="setStatus"
          />
        </div>
      </div>
    </template>
  </card>
</template>

<script lang="ts">
import {
  defineComponent, PropType,
} from 'vue';
import { useI18n } from 'vue-i18n';
import { ToDoItem, Status } from '@/types/shared';
import HabitStatus from '@/components/habit/HabitStatus.vue';
import Card from '@/components/Card.vue';

export default defineComponent({
  components: {
    HabitStatus,
    Card,
  },
  props: {
    todo: {
      type: Object as PropType<ToDoItem>,
      required: true,
    },
  },
  setup(props, ctx) {
    const { t } = useI18n();

    const setStatus = (newStatus: Status) => {
      // console.log(newStatus);
      ctx.emit('statusUpdate', {
        id: props.todo.id,
        status: props.todo.status === newStatus ? 'open' : newStatus,
      });
    };

    return {
      t,
      setStatus,
    };
  },
});
</script>

