
import { defineComponent, ref } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
import AppButton from '@/components/buttons/AppButton.vue';
import Modal from '@/components/Modal.vue';
import SelectField from '@/components/forms/SelectField.vue';
import { createRoute, habitRoute } from '@/services/utils/link';
import ButtonGroup from '../ButtonGroup.vue';
import { AppError, ListDayType } from '@/types/shared';

export default defineComponent({
  components: {
    AppButton,
    Modal,
    SelectField,
    ButtonGroup,
  },
  setup() {
    const store = useStore();
    const { t } = useI18n();
    const show = ref(false);
    const selectedOption = ref();
    const router = useRouter();
    const hasError = ref(false);
    const disabled = ref({
      [ListDayType.Today]: false,
      [ListDayType.Tomorrow]: false,
      [ListDayType.DailyHabit]: false,
    });

    const checkIfExists = async (type: ListDayType) => {
      const result = await store.dispatch('todos/check', type);

      if (result instanceof AppError) {
        return;
      }

      disabled.value[type] = result;
    };

    const triggerChecks = () => {
      checkIfExists(ListDayType.Tomorrow);
      checkIfExists(ListDayType.Today);
    };

    const showModal = () => {
      show.value = true;
      triggerChecks();
    };

    triggerChecks();

    const submit = () => {
      if (!selectedOption.value) {
        hasError.value = true;
        return;
      }

      show.value = false;
      if (selectedOption.value === 'habit') {
        router.push({
          path: habitRoute.link,
          query: {
            add: 'true',
          },
        });
      } else {
        router.push({
          path: createRoute.link,
          query: {
            mode: 'create',
            type: selectedOption.value,
          },
        });
      }
      selectedOption.value = '';
    };

    const clearError = () => {
      hasError.value = false;
    };

    return {
      disabled,
      clearError,
      hasError,
      selectedOption,
      submit,
      show,
      showModal,
      t,
    };
  },
});
