
import { defineComponent, PropType } from 'vue';
import { useStore } from 'vuex';
import { AppError, Status, ToDoList } from '@/types/shared';
import DailyHabitItem from './DailyHabitItem.vue';

export default defineComponent({
  components: { DailyHabitItem },
  props: {
    todoList: {
      type: Object as PropType<ToDoList>,
      required: true,
    },
  },
  setup(props, ctx) {
    const store = useStore();

    const statusUpdate = async (params: {id: string | number, status: Status}) => {
      const result = await store.dispatch('habits/updateStatus', {
        id: params.id,
        status: params.status,
      });

      if (result instanceof AppError) {
        console.log(result);
        return;
      }

      ctx.emit('refresh');
    };

    return {
      statusUpdate,
    };
  },
});
