<template>
  <div class="panel">
    <div class="panel__header">
      <div
        class="panel__title"
        data-id="panel-title-left"
        @click="onClick('left')"
        :class="{
          'panel--active': isLeft,
          'panel--empty': emptyLeft
        }"
      >
        <span class="panel__title-text">
          <slot name="title-left"/>
        </span>
      </div>
      <div
        class="panel__title"
        data-id="panel-title-right"
        @click="onClick('right')"
        :class="{
          'panel--active': !isLeft,
          'panel--empty': emptyRight
        }"
      >
        <span class="panel__title-text">
          <slot name="title-right"/>
        </span>
      </div>
    </div>
    <div class="panel__content">
      <div v-if="isLeft" class="panel__left">
        <slot name="left"/>
      </div>
      <div v-else class="panel__right">
        <slot name="right"/>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import {
  defineComponent, PropType, ref, watch,
} from 'vue';
import { Position } from '@/types/shared';

export default defineComponent({
  props: {
    active: {
      type: String as PropType<Position>,
      default: Position.Left,
    },
  },
  setup(props, ctx) {
    const active = ref(props.active);
    const isLeft = ref(props.active === Position.Left);
    const contentClass = ref({});
    const onClick = (position: Position) => {
      active.value = position;
    };

    watch(active, (newValue) => {
      isLeft.value = newValue === Position.Left;
    });

    const emptyLeft = !ctx.slots['title-left'];
    const emptyRight = !ctx.slots['title-right'];

    return {
      onClick,
      isLeft,
      contentClass,
      emptyLeft,
      emptyRight,
    };
  },
});
</script>

<style lang="scss" scoped>
@import '@/assets/styles/utils/_functions.scss';

.panel {
  &__header {
    @apply flex border-b border-gray-200 mb-6 border-dashed;
  }

  &__title {
    @apply py-2 px-4 rounded-full cursor-default bg-gray-100
    font-semibold text-base mb-2 mr-2 overflow-hidden text-center;
    min-width: remSize(100px);
    max-width: remSize(150px);
  }

  &--empty {
    @apply p-0;
    width: 0;
    min-width: 0;
  }

  &--active {
    @apply bg-pink-900 bg-opacity-40 text-white;
  }
}
</style>
