/* eslint-disable import/prefer-default-export */
export const getDaysDiff = (start: number, end: number) : number => {
  const daysDiff = Math.floor((end - start) / (1000 * 60 * 60 * 24));
  return daysDiff < 0 ? 0 : daysDiff;
};

export const getDateString = (dateValue: string | number | Date): string => {
  try {
    if (dateValue === 0) throw new Error();
    const date = new Date(dateValue);
    const doubleDigit = (digit: number) => (digit.toString().length === 1 ? `0${digit}` : digit);
    return `${date.getFullYear()}-${doubleDigit(date.getMonth() + 1)}-${doubleDigit(date.getDate())}`;
  } catch {
    return '';
  }
};

export const getLocalDate = (dateValue: string | number | Date, locale?: string): string => {
  try {
    if (dateValue === 0) throw new Error();
    const date = new Date(dateValue);
    return date.toLocaleDateString();
  } catch {
    return '';
  }
};
