export type Day = 'mon' | 'tue' | 'wed' | 'thu' | 'fri' | 'sat' | 'sun';

export const dayMap: Record<number, Day> = {
  0: 'sun',
  1: 'mon',
  2: 'tue',
  3: 'wed',
  4: 'thu',
  5: 'fri',
  6: 'sat',
};

export type Status = 'done' | 'open' | 'progress' | 'skip' | 'missed'

export interface Category {
  id: string | number,
  name: string,
}

export enum ListDayType {
  Tomorrow = 'Tomorrow',
  Today = 'Today',
  DailyHabit = 'DailyHabit'
}
export interface ToDoItem {
  id?: string | number,
  key?: string,
  description: string,
  status: Status,
  days?: Day[],
  startTime?: string,
  endTime?: string,
  // carry over to the next day
  carryOver: boolean,
  habit?: boolean,
  optional: boolean
  // Used exclusively for habit to archive
  save?: boolean
  categoryId?: string | number,
}

export interface Habit {
  id?: string | number,
  description: string,
  days: Day[],
  startTime?: string,
  endTime?: string,
  endDate?: number,
  createdOn?: number
  deletedOn?: number
  save: boolean
  categoryId?: string | number,
}

export type ToDoItemForm = ToDoItem & {
  endDate?: string,
}

export type ToDoItemExtension = ToDoItem & { checked?: boolean};
export interface HabitCount {
  id?: string | number,
  habitId: string | number,
  date: number,
  status: Status
}

export type HabitDetail = Habit & {
  count?: {
    skipped: number,
    done: number,
    missed: number
  }
}

export interface ToDoList {
  id?: string | number,
  createdOn?: number,
  startDate?: number,
  // Date used to expire list in storage
  expireOn?: number,
  // Indicator to archive this list
  save: boolean
  // List of items
  todos: Array<ToDoItem>,
}

export enum ArchiveType {
  Habit = 'habit',
  TodoList = 'todoList'
}

export type Archive = {
  [ArchiveType.Habit]: HabitDetail[],
  [ArchiveType.TodoList]: ToDoList[]
}

export enum ErrorCode {
  General = 'General',
  AlreadyExists = 'AlreadyExists',
  FetchFailed = 'fetchFailed',
  FetchAllFailed = 'fetchAllFailed',
  NotFound = 'NotFound',
  InsertFailed = 'insertFailed',
  MaxCapacity = 'maxCapacity',
  RemoveFailed = 'removeFailed',
  RemoveAllFailed = 'removeAllFailed',
  UpdateFailed = 'updateFailed',
  ArchiveFailed = 'archiveFailed',
  PartialRemoval = 'partialRemoval',
  CheckFailed = 'checkFailed',
}

type AppErrorTypeKey = 'habit' | 'habitCount' | 'todo' | 'archive' | 'general'

type AppErrorType = {
  type: AppErrorTypeKey,
  code: ErrorCode,
}

export class AppError {
  type: AppErrorTypeKey

  code: ErrorCode

  constructor(props: AppErrorType) {
    this.type = props.type;
    this.code = props.code;
  }
}

export enum Position {
  Left = 'left',
  Right = 'right'
}
