/* eslint-disable class-methods-use-this */
import { v4 as uuid } from 'uuid';
import {
  ToDoItem,
  ToDoList,
  ErrorCode,
  ListDayType,
  Habit, HabitCount, AppError, Day, ArchiveType, HabitDetail, Status,
} from '@/types/shared';
import ArchiveService from './archiveService';
import storage from './storage';
import { STORAGE_KEYS } from './utils/helpers';
import AbstractHabitService from './abstracts/abstractHabitService';

const localStorage = storage.local;
const archiveSerive = new ArchiveService();

export type ListObj = Record<string, ToDoList>

export default class HabitService extends AbstractHabitService {
  updateStatus(status: Status, id: string | number): Promise<ToDoItem | AppError> {
    try {
      const result = localStorage.get(STORAGE_KEYS.todos) as ListObj;

      const index = result[ListDayType.DailyHabit].todos
        .findIndex((todoItem) => todoItem.id === id);
      if (index === -1) {
        return Promise.resolve(new AppError({
          type: 'habit',
          code: ErrorCode.UpdateFailed,
        }));
      }
      result[ListDayType.DailyHabit].todos[index].status = status;
      localStorage.set(STORAGE_KEYS.todos, result);

      return Promise.resolve(result[ListDayType.DailyHabit].todos[index]);
    } catch {
      return Promise.resolve(new AppError({
        type: 'habit',
        code: ErrorCode.UpdateFailed,
      }));
    }
  }

  updateHabit(habitToUpdate: Habit): Promise<Habit | AppError> {
    try {
      const habits = localStorage.get(STORAGE_KEYS.habit) as Habit[];
      const index = habits.findIndex((habit) => habit.id === habitToUpdate.id);

      if (index === -1) throw new Error();

      habits[index] = habitToUpdate;

      localStorage.set(STORAGE_KEYS.habit, habits);

      return Promise.resolve(habitToUpdate);
    } catch {
      return Promise.resolve(new AppError({
        type: 'habit',
        code: ErrorCode.UpdateFailed,
      }));
    }
  }

  addHabitCount(count: HabitCount): Promise<HabitCount | AppError> {
    try {
      const result = localStorage.get(STORAGE_KEYS.habitCount) as HabitCount[];
      const payload = count;
      payload.id = uuid();
      result.push(payload);
      localStorage.set(STORAGE_KEYS.habitCount, result);

      return Promise.resolve(payload);
    } catch {
      return Promise.resolve(new AppError({
        type: 'habit',
        code: ErrorCode.InsertFailed,
      }));
    }
  }

  addHabitCounts(list: ToDoList): Promise<HabitCount[] | AppError> {
    try {
      const result = localStorage.get(STORAGE_KEYS.habitCount) as HabitCount[];
      const counts: HabitCount[] = list.todos.map<HabitCount>((todo) => ({
        id: uuid(),
        habitId: todo.id || 0,
        date: list.startDate || 0,
        status: todo.status === 'progress' || todo.status === 'open' ? 'missed' : todo.status,
      }));
      const payload = [...result, ...counts];
      localStorage.set(STORAGE_KEYS.habitCount, payload);

      return Promise.resolve(payload);
    } catch {
      return Promise.resolve(new AppError({
        type: 'habit',
        code: ErrorCode.InsertFailed,
      }));
    }
  }

  addHabit(habit: Habit): Promise<Habit | AppError> {
    try {
      const result = localStorage.get(STORAGE_KEYS.habit) as Habit[] || [];
      const payload = habit;
      payload.id = uuid();
      result.push(payload);
      localStorage.set(STORAGE_KEYS.habit, result);

      return Promise.resolve(payload);
    } catch {
      return Promise.resolve(new AppError({
        type: 'habit',
        code: ErrorCode.InsertFailed,
      }));
    }
  }

  getHabit(id: string | number): Promise<Habit | AppError> {
    try {
      const habits = localStorage.get(STORAGE_KEYS.habit) as Habit[];
      const habit = habits.find((item) => item.id === id);

      if (!habit) throw new Error();

      return Promise.resolve(habit);
    } catch {
      return Promise.resolve(new AppError({
        type: 'habit',
        code: ErrorCode.FetchFailed,
      }));
    }
  }

  async removeTodo(id: string | number): Promise<void> {
    const listObj = localStorage.get(STORAGE_KEYS.todos) as ListObj;
    const dailyList = listObj[ListDayType.DailyHabit];
    if (!listObj[ListDayType.DailyHabit].todos.find((todoItem) => todoItem.id === id)) {
      return;
    }

    listObj[ListDayType.DailyHabit].todos = (
      dailyList.todos.filter((todoItem) => todoItem.id !== id)
    );

    localStorage.set(STORAGE_KEYS.todos, dailyList);
  }

  async removeHabit(id: string | number, force = false): Promise<void | AppError> {
    try {
      const habit = await this.getHabit(id) as Habit;
      const habitCount = await this.getHabitCount(id, null, true) as HabitCount[];

      if (habit instanceof AppError) throw new Error();
      if (habitCount instanceof AppError && habitCount.code !== ErrorCode.NotFound) {
        throw new Error();
      }

      const habitDetail: HabitDetail = {
        ...habit,
        ...(Array.isArray(habitCount) && {
          count: {
            missed: habitCount.filter((c) => c.status === 'missed').length,
            skipped: habitCount.filter((c) => c.status === 'skip').length,
            done: habitCount.filter((c) => c.status === 'done').length,
          },
        }),
      };

      const hasNotExpired = Date.now() < (habitDetail?.endDate || 0);
      if (habitDetail?.endDate && hasNotExpired) {
        habitDetail.deletedOn = Date.now();
      }

      this.removeTodo(id);

      const habits = localStorage.get(STORAGE_KEYS.habit) as Habit[];
      localStorage.set(STORAGE_KEYS.habit, habits.filter((aHabit) => aHabit.id !== habitDetail.id));

      if (habitCount && habitCount.length) {
        const habitCounts = localStorage.get(STORAGE_KEYS.habitCount) as HabitCount[];
        localStorage
          .set(STORAGE_KEYS.habitCount, habitCounts.filter((count) => count.id !== habitDetail.id));
      }

      if (!force && habitDetail.save) {
        const archiveResult = await archiveSerive.addToArchive(habitDetail, ArchiveType.Habit);
        if (archiveResult instanceof AppError) {
          return Promise.resolve(new AppError({
            type: 'todo',
            code: ErrorCode.ArchiveFailed,
          }));
        }
      }

      return Promise.resolve();
    } catch {
      return Promise.resolve(new AppError({
        type: 'habit',
        code: ErrorCode.RemoveFailed,
      }));
    }
  }

  createDailyHabitTodos(day: Day): Promise<ToDoList | AppError> {
    try {
      const habits = localStorage.get(STORAGE_KEYS.habit) as Habit[];
      let listObj = localStorage.get(STORAGE_KEYS.todos) as ListObj;

      if (!habits) {
        return Promise.resolve(new AppError({
          type: 'habit',
          code: ErrorCode.NotFound,
        }));
      }

      const currentDate = Date.now();
      const DEFAULTS: ToDoList = {
        createdOn: currentDate,
        startDate: currentDate,
        expireOn: (new Date(currentDate)).setHours(24),
        id: uuid(),
        save: false,
        todos: [],
      };

      if (!listObj || !listObj[ListDayType.DailyHabit]
        || !listObj[ListDayType.DailyHabit].todos.length) {
        listObj = {
          [ListDayType.DailyHabit]: DEFAULTS,
        };
      }

      if (habits) {
        const todos = habits.filter((habit) => habit.days.includes(day))
          .map<ToDoItem>((habit) => ({
            id: habit.id,
            habit: true,
            carryOver: false,
            endTime: habit.endTime,
            startTime: habit.startTime,
            status: 'open',
            optional: false,
            description: habit.description,
          }));

        if (todos.length === 0) {
          return Promise.resolve(new AppError({
            type: 'habit',
            code: ErrorCode.NotFound,
          }));
        }

        const ids = listObj[ListDayType.DailyHabit].todos.map((todoItem) => todoItem.id);
        todos.forEach((todoItem) => {
          if (!ids.includes(todoItem.id)) {
            listObj[ListDayType.DailyHabit].todos.push(todoItem);
          }
        });
        localStorage.set(STORAGE_KEYS.todos, listObj);
      }

      return Promise.resolve(listObj[ListDayType.DailyHabit] || []);
    } catch {
      return Promise.resolve(new AppError({
        type: 'habit',
        code: ErrorCode.InsertFailed,
      }));
    }
  }

  getHabits(): Promise<Habit[] | AppError> {
    try {
      const habits = localStorage.get(STORAGE_KEYS.habit);
      if (habits) {
        return Promise.resolve(habits as Habit[]);
      }

      return Promise.resolve(new AppError({
        type: 'habit',
        code: ErrorCode.NotFound,
      }));
    } catch {
      return Promise.resolve(new AppError({
        type: 'habit',
        code: ErrorCode.FetchAllFailed,
      }));
    }
  }

  getHabitCount(id: string | number, year?: number | null, desc = false)
  : Promise<HabitCount[] | AppError> {
    try {
      const habits = localStorage.get(STORAGE_KEYS.habitCount) as HabitCount[];
      if (habits) {
        let filteredHabits: HabitCount[] = habits
          .filter((habit) => habit.habitId === id)
          .filter((habit) => !Number.isNaN(year) || new Date(habit.date).getFullYear() === year);

        if (desc) {
          filteredHabits = filteredHabits.sort((a, b) => b.date - a.date);
        } else {
          filteredHabits = filteredHabits.sort((a, b) => a.date - b.date);
        }
        if (filteredHabits.length > 0) {
          return Promise.resolve(filteredHabits);
        }
      }
      return Promise.resolve(new AppError({
        type: 'habitCount',
        code: ErrorCode.NotFound,
      }));
    } catch {
      return Promise.resolve(new AppError({
        type: 'habitCount',
        code: ErrorCode.FetchFailed,
      }));
    }
  }

  removeExpiredHabits(): Promise<void> {
    try {
      const habits = localStorage.get(STORAGE_KEYS.habit) as HabitCount[];
      // eslint-disable-next-line no-return-await
      habits.forEach(async (habit) => Date.now() >= habit.date && await this.removeHabit(habit.id || ''));
    } catch (ex) {
      console.warn(ex);
    }
    return Promise.resolve();
  }
}
