<template>
  <div class="alert" :class="classes">
    <h3 class="alert__title">
      <slot name="title"/>
    </h3>
    <p class="alert__content">
      <slot />
    </p>
    <button v-if="closeable" type="button" class="alert__close" @click="$emit('close')">
      <svg xmlns="http://www.w3.org/2000/svg" width="15" height="15" viewBox="0 0 67 67" fill="none">
        <path d="M3 64L64 3" stroke="black" stroke-width="6"/>
        <path d="M3 3L64 64" stroke="black" stroke-width="6"/>
      </svg>
    </button>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType } from 'vue';

export type AlertVariant = 'warning' | 'info' | 'success' | 'error';

export default defineComponent({
  props: {
    variant: {
      type: String as PropType<AlertVariant>,
      default: 'info',
    },
    closeable: {
      type: Boolean,
      default: true,
    },
  },
  setup(props) {
    const classes = {
      [`alert--${props.variant}`]: true,
    };

    return {
      classes,
    };
  },
});
</script>

<style lang="scss" scoped>
.alert {
  @apply p-3 mt-3 rounded relative;

  &__title {
    @apply font-semibold pb-1  mr-3;
  }

  &__content {
    @apply text-sm mr-3;
  }

  &__close {
    @apply absolute top-3 right-3;
  }

  &--warning {
    @apply text-yellow-500 bg-yellow-50 border border-yellow-300;

    path {
      stroke: rgba(252, 211, 77, var(--tw-border-opacity));
    }
  }

  &--info {
    @apply text-blue-500 bg-blue-50 border border-blue-300;

    path {
      stroke: rgba(147, 197, 253, var(--tw-border-opacity));
    }
  }
  &--error {
    @apply text-red-500 bg-red-50 border border-red-300;

    path {
      stroke: rgba(252, 165, 165, var(--tw-border-opacity));
    }
  }
  &--success {
    @apply text-green-500 bg-green-50 border border-green-300;

    path {
      stroke: rgba(110, 231, 18, var(--tw-border-opacity));
    }
  }
}
</style>
