import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router';
import Home from '../views/Home.vue';

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/overview',
    name: 'Overview',
    component: Home,
  },
  {
    path: '/archive-list',
    name: 'ArchiveList',
    component: () => import('../views/ArchiveList.vue'),
  },
  {
    path: '/archive-habit',
    name: 'ArchiveHabit',
    component: () => import(/* webpackChunkName: "Archive" */ '../views/ArchiveHabit.vue'),
  },
  {
    path: '/habits',
    name: 'Habits',
    // route level code-splitting
    // this generates a separate chunk (archive.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "Archive" */ '../views/Habits.vue'),
  },
  {
    path: '/habit/:id',
    name: 'Habit',
    component: () => import('../views/Habit.vue'),
  },
  {
    path: '/create',
    name: 'Create',
    component: () => import('../views/Create.vue'),
  },
  {
    path: '/analysis',
    name: 'Analysis',
    component: () => import('../views/Analysis.vue'),
  },
  // {
  //   path: '*',
  //   name: 'NotFound',
  //   component: () => import('../views/Error.vue'),
  // },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
