
import {
  defineComponent, PropType,
} from 'vue';
import { useI18n } from 'vue-i18n';
import { ToDoItem, Status } from '@/types/shared';
import HabitStatus from '@/components/habit/HabitStatus.vue';
import Card from '@/components/Card.vue';

export default defineComponent({
  components: {
    HabitStatus,
    Card,
  },
  props: {
    todo: {
      type: Object as PropType<ToDoItem>,
      required: true,
    },
  },
  setup(props, ctx) {
    const { t } = useI18n();

    const setStatus = (newStatus: Status) => {
      // console.log(newStatus);
      ctx.emit('statusUpdate', {
        id: props.todo.id,
        status: props.todo.status === newStatus ? 'open' : newStatus,
      });
    };

    return {
      t,
      setStatus,
    };
  },
});
