/* eslint-disable class-methods-use-this */
import { v4 as uuid } from 'uuid';
import {
  ToDoItem,
  ToDoList,
  ErrorCode,
  ListDayType,
  AppError, ArchiveType, Status,
} from '@/types/shared';
import storage from './storage';
import ArchiveService from './archiveService';
import { STORAGE_KEYS, ListObj } from './utils/helpers';

const localStorage = storage.local;
const archiveService = new ArchiveService();
export interface ToDoServiceInt {
  getTodos(type: ListDayType): Promise<ToDoList | AppError>;
  createList(type: ListDayType, list: ToDoList, carryOverList?: ToDoList)
  : Promise<ToDoList | AppError>;
  saveList(type: ListDayType, save: boolean): Promise<ToDoList | AppError>;
  addTodo(type: ListDayType, todo: ToDoItem): Promise<ToDoItem | AppError>;
  updateTodo(type: ListDayType, todo: ToDoItem): Promise<ToDoItem | AppError>;
  removeTodo(type: ListDayType, id: string | number): Promise<ToDoItem | AppError>;
  deleteTodos(type: ListDayType, ids: (string | number)[]): Promise<boolean | AppError>;
  deleteTodoList(type: ListDayType, force?: boolean): Promise<ToDoList | AppError>;
  updateTodoStatus(status: Status, id: string | number): Promise<ToDoItem | AppError>
  hasTodoList(type: ListDayType): Promise<boolean | AppError>
}

export default class ToDoService implements ToDoServiceInt {
  deleteTodos(type: ListDayType, ids: (string | number)[]): Promise<boolean | AppError> {
    try {
      const result = localStorage.get(STORAGE_KEYS.todos) as ListObj;

      result[type].todos = result[type].todos.filter((todoItem) => !ids.includes(todoItem.id || ''));
      localStorage.set(STORAGE_KEYS.todos, result);

      return Promise.resolve(true);
    } catch {
      return Promise.resolve(new AppError({
        type: 'todo',
        code: ErrorCode.RemoveAllFailed,
      }));
    }
  }

  hasTodoList(type: ListDayType): Promise<boolean | AppError> {
    try {
      const result = localStorage.get(STORAGE_KEYS.todos) as ListObj;

      if (result && result[type] && !!result[type].createdOn) {
        return Promise.resolve(true);
      }

      return Promise.resolve(false);
    } catch {
      return Promise.resolve(new AppError({
        type: 'todo',
        code: ErrorCode.CheckFailed,
      }));
    }
  }

  updateTodoStatus(status: Status, id: string | number): Promise<ToDoItem | AppError> {
    try {
      const result = localStorage.get(STORAGE_KEYS.todos) as ListObj;

      const index = result[ListDayType.Today].todos.findIndex((todoItem) => todoItem.id === id);
      if (index === -1) {
        return Promise.resolve(new AppError({
          type: 'todo',
          code: ErrorCode.UpdateFailed,
        }));
      }
      result[ListDayType.Today].todos[index].status = status;
      localStorage.set(STORAGE_KEYS.todos, result);

      return Promise.resolve(result[ListDayType.Today].todos[index]);
    } catch {
      return Promise.resolve(new AppError({
        type: 'todo',
        code: ErrorCode.UpdateFailed,
      }));
    }
  }

  async deleteTodoList(type: ListDayType, force = false): Promise<AppError | ToDoList> {
    try {
      const result = localStorage.get(STORAGE_KEYS.todos) as ListObj;
      const deleted = result[type];
      delete result[type];

      if (force === false && deleted.save) {
        const archiveResult = await archiveService.addToArchive(deleted, ArchiveType.TodoList);
        if (archiveResult instanceof AppError) {
          return Promise.resolve(new AppError({
            type: 'todo',
            code: ErrorCode.ArchiveFailed,
          }));
        }
      }

      localStorage.set(STORAGE_KEYS.todos, result);
      return Promise.resolve(deleted);
    } catch {
      return Promise.resolve(new AppError({
        type: 'todo',
        code: ErrorCode.RemoveAllFailed,
      }));
    }
  }

  removeTodo(type: ListDayType, id: string | number):
  Promise<ToDoItem | AppError> {
    try {
      const result = localStorage.get(STORAGE_KEYS.todos) as ListObj;

      const toDelete = result[type].todos.find((todoItem) => todoItem.id === id);
      if (!toDelete) throw new Error();

      result[type].todos = result[type].todos.filter((todoItem) => todoItem.id !== id);
      localStorage.set(STORAGE_KEYS.todos, result);

      return Promise.resolve(toDelete);
    } catch {
      return Promise.resolve(new AppError({
        type: 'todo',
        code: ErrorCode.RemoveFailed,
      }));
    }
  }

  updateTodo(type: ListDayType, todo: ToDoItem): Promise<ToDoItem | AppError> {
    try {
      const result = localStorage.get(STORAGE_KEYS.todos) as ListObj;

      const index = result[type].todos.findIndex((todoItem) => todoItem.id === todo.id);
      if (index === -1) {
        return Promise.resolve(new AppError({
          type: 'todo',
          code: ErrorCode.UpdateFailed,
        }));
      }
      result[type].todos[index] = { ...result[type].todos[index], ...todo };
      localStorage.set(STORAGE_KEYS.todos, result);

      return Promise.resolve(result[type].todos[index]);
    } catch {
      return Promise.resolve(new AppError({
        type: 'todo',
        code: ErrorCode.UpdateFailed,
      }));
    }
  }

  addTodo(type: ListDayType, todo: ToDoItem): Promise<ToDoItem | AppError> {
    try {
      const result = localStorage.get(STORAGE_KEYS.todos) as ListObj;
      const payload = todo;
      payload.id = uuid();
      payload.key = `todo-${payload.id}`;
      result[type].todos.push(payload);
      localStorage.set(STORAGE_KEYS.todos, result);

      return Promise.resolve(payload);
    } catch {
      return Promise.resolve(new AppError({
        type: 'todo',
        code: ErrorCode.InsertFailed,
      }));
    }
  }

  saveList(type: ListDayType, save: boolean): Promise<ToDoList | AppError> {
    try {
      const result = localStorage.get(STORAGE_KEYS.todos) as ListObj;
      result[type].save = save;
      localStorage.set(STORAGE_KEYS.todos, result);

      return Promise.resolve(result[type]);
    } catch {
      return Promise.resolve(new AppError({
        type: 'todo',
        code: ErrorCode.UpdateFailed,
      }));
    }
  }

  createList(type: ListDayType, list: ToDoList, carryOverList?: ToDoList)
  : Promise<ToDoList | AppError> {
    try {
      let listObj: ListObj;
      const result = localStorage.get(STORAGE_KEYS.todos);
      const currentDate = Date.now();

      const DEFAULTS: Omit<ToDoList, 'id'> = {
        save: false,
        todos: [],
      };

      if (!result) {
        listObj = {
          [type]: { ...DEFAULTS, ...{ id: uuid() } },
        };
      } else {
        listObj = result as ListObj;

        if (listObj[type]) {
          return Promise.resolve(new AppError({
            type: 'todo',
            code: ErrorCode.AlreadyExists,
          }));
        }

        listObj[type] = { ...DEFAULTS, ...{ id: uuid() } };
      }

      listObj[type].save = list.save;
      listObj[type].expireOn = type === ListDayType.Tomorrow
        ? (new Date(currentDate)).setHours(48)
        : (new Date(currentDate)).setHours(24);
      listObj[type].startDate = type === ListDayType.Tomorrow
        ? (new Date(currentDate)).setHours(24)
        : currentDate;
      listObj[type].todos = list.todos.map((todo) => {
        const id = uuid();
        return { ...todo, ...{ id, key: todo.habit ? `habit-${id}` : `todo-${id}` } };
      });
      listObj[type].createdOn = currentDate;

      if (carryOverList) {
        const carryOverItems = carryOverList.todos.filter((todoItem) => todoItem.status !== 'done' && todoItem.carryOver);
        listObj[type].todos = [
          ...listObj[type].todos,
          ...carryOverItems,
        ];
      }

      localStorage.set(STORAGE_KEYS.todos, listObj);
      return Promise.resolve(listObj[type]);
    } catch {
      return Promise.resolve(new AppError({
        type: 'todo',
        code: ErrorCode.InsertFailed,
      }));
    }
  }

  async getTodos(type: ListDayType): Promise<ToDoList | AppError> {
    const errorType = type === ListDayType.DailyHabit ? 'habit' : 'todo';

    try {
      const listObj = localStorage.get(STORAGE_KEYS.todos) as ListObj;

      if (!listObj || !listObj[type] || !listObj[type].createdOn) {
        return Promise.resolve(new AppError({
          type: errorType,
          code: ErrorCode.NotFound,
        }));
      }

      return Promise.resolve((listObj as ListObj)[type]);
    } catch {
      return Promise.resolve(new AppError({
        type: errorType,
        code: ErrorCode.FetchAllFailed,
      }));
    }
  }
}
