<template>
  <div class="overview">
    <page-title>
      {{t('overviewPage.title')}}
      <template #sub>{{t('overviewPage.pageDescription')}}</template>
    </page-title>
    <card-group>
      <card class="overview__habit" flat fill>
        <template #title>
          {{t('overviewPage.habits.title')}}
        </template>
        <template #body>
          <daily-habit-items
            v-if="todos.DailyHabit && todos.DailyHabit.todos"
            :todoList="todos.DailyHabit"
            @refresh="fetchList(habitListType)"
          />
          <template v-else>
            <p class="overview__empty-description">{{t('overviewPage.habits.empty')}}</p>
            <router-button to="/habits">
              {{t('overviewPage.buttons.habit')}}
            </router-button>
          </template>
        </template>
      </card>
      <card class="overview__todos" flat fill>
        <template #title>
          {{t('overviewPage.todos.title')}}
        </template>
        <template #body>
          <panel>
            <template #title-left>
              {{t('overviewPage.todos.todayLabel')}}
            </template>
            <template #title-right>
              {{t('overviewPage.todos.tomorrowLabel')}}
            </template>
            <template #left>
              <todo-items
                v-if="todos.Today && todos.Today.todos"
                :todoList="todos.Today"
                :listType="todayListType"
                @refresh="fetchList(todayListType)"
              />
              <template v-else>
                <p class="overview__empty-description">{{t('overviewPage.todos.empty')}}</p>
                <router-button
                  :to="{path: '/create', query: {type: 'Today'}}"
                >
                {{t('overviewPage.buttons.createList')}}
                </router-button>
              </template>
            </template>
            <template #right>
              <todo-items
                v-if="todos.Tomorrow && todos.Tomorrow.todos"
                :todoList="todos.Tomorrow"
                :listType="tomorrowListType"
                @refresh="fetchList(tomorrowListType)"
              />
              <template v-else>
                <p class="overview__empty-description">{{t('overviewPage.todos.emptyTomorrow')}}</p>
                <router-button
                  :to="{path: '/create', query: {type: 'Tomorrow'}}"
                >
                {{t('overviewPage.buttons.createList')}}
                </router-button>
              </template>
            </template>
          </panel>
        </template>
      </card>
    </card-group>
  </div>
</template>

<script lang="ts">
import {
  defineComponent, onMounted, reactive, ref,
} from 'vue';
import { useI18n } from 'vue-i18n';
import { useStore } from 'vuex';
import PageTitle from '@/components/PageTitle.vue';
import TodoItems from '@/components/todo/TodoItems.vue';
import {
  AppError, ErrorCode, ListDayType, ToDoList,
} from '@/types/shared';
import Panel from '@/components/Panel.vue';
import Card from '@/components/Card.vue';
import CardGroup from '@/components/CardGroup.vue';
import Alert from '@/components/Alert.vue';
import RouterButton from '@/components/buttons/RouterButton.vue';
import DailyHabitItems from '@/components/habit/DailyHabitItems.vue';

export default defineComponent({
  name: 'Home',
  components: {
    PageTitle,
    TodoItems,
    Panel,
    Card,
    CardGroup,
    RouterButton,
    DailyHabitItems,
    // Alert,
  },
  setup() {
    const { t } = useI18n();
    const store = useStore();
    const todoListToday = ref<ToDoList | null>(null);
    const todoTomorrowList = ref<ToDoList | null>(null);
    const habitsListToday = ref<ToDoList | null>(null);
    const todos = reactive({
      [ListDayType.Today]: todoListToday,
      [ListDayType.Tomorrow]: todoTomorrowList,
      [ListDayType.DailyHabit]: habitsListToday,
    });
    const errors = ref({
      [ListDayType.Today]: '',
      [ListDayType.Tomorrow]: '',
      [ListDayType.DailyHabit]: '',
    });

    const fetchList = (type: ListDayType) => {
      store.dispatch('todos/fetch', type).then((result) => {
        if (result instanceof AppError && !ErrorCode.NotFound) {
          errors.value[type] = result.code;
          return;
        }

        todos[type] = store.state.todos[type];
      });
    };

    onMounted(async () => {
      await fetchList(ListDayType.Today);
      await fetchList(ListDayType.Tomorrow);
      await fetchList(ListDayType.DailyHabit);
    });

    return {
      t,
      fetchList,
      errors,
      todos,
      habitListType: ListDayType.DailyHabit,
      todayListType: ListDayType.Today,
      tomorrowListType: ListDayType.Tomorrow,
    };
  },
});
</script>

<style lang="scss">
  .overview {
    .expand {
      width: 5px;
      display: inline-block;
    }

    &__empty-description {
      @apply mb-4 text-gray-600 italic;
    }
  }
</style>
