import { ActionTree, MutationTree } from 'vuex';
import {
  AppError, dayMap, Habit, HabitCount, Status,
} from '@/types/shared';
import { RootState } from './types';
import HabitService from '@/services/habitService';

const service = new HabitService();

const habitState = () => ({
  habits: [] as Habit[],
  currentHabitCount: [] as HabitCount[],
  currentHabit: {} as Habit,
});

export type HabitState = ReturnType<typeof habitState>;

export const mutations: MutationTree<HabitState> = {
  UPDATE(state, payload: Habit[]) {
    state.habits = payload;
  },
  UPDATE_CURRENT(state, payload: Habit) {
    state.currentHabit = payload;
  },
  UPDATE_COUNT(state, payload: HabitCount[]) {
    state.currentHabitCount = payload;
  },
};

export const actions: ActionTree<HabitState, RootState> = {
  async createDailyList({ commit }) {
    const currentDay = dayMap[(new Date()).getDay()];
    const result = await service.createDailyHabitTodos(currentDay);

    if (result instanceof AppError) return result;

    return true;
  },
  async fetch({ commit }) {
    const result: AppError | Habit[] = await service.getHabits();

    if (!(result instanceof AppError)) {
      commit('UPDATE', result);
    }

    return result;
  },
  async fetchCount({ commit }, { id, year }: {id: string | number, year: number}) {
    const result: AppError | HabitCount[] = await service.getHabitCount(id, year);

    if (!(result instanceof AppError)) {
      commit('UPDATE_COUNT', result);
    }

    return result;
  },
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  async delete({ commit }, id: string | number) {
    const result: AppError | void = await service.removeHabit(id);

    if (result instanceof AppError) return result;

    return true;
  },
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  async add({ commit }, payload: Habit) {
    const result: AppError | Habit = await service.addHabit(payload);

    if (result instanceof AppError) return result;

    return true;
  },
  async get({ commit }, payload: string) {
    const result: AppError | Habit = await service.getHabit(payload);

    if (result instanceof AppError) return result;

    commit('UPDATE_CURRENT', result);

    return true;
  },
  // eslint-disable-next-line no-empty-pattern
  async update({}, payload: Habit) {
    const result = await service.updateHabit(payload);

    if (result instanceof AppError) return result;

    return true;
  },
  // eslint-disable-next-line no-empty-pattern
  async updateStatus({ }, payload: {id: string | number, status: Status}) {
    const result = await service.updateStatus(payload.status, payload.id);

    if (result instanceof AppError) return result;

    return true;
  },
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  async addCount({ commit }, payload: HabitCount) {
    const result: AppError | HabitCount = await service.addHabitCount(payload);

    if (result instanceof AppError) return result;

    return true;
  },
};

export default {
  namespaced: true,
  state: habitState,
  mutations,
  actions,
};
