
import {
  defineComponent, computed, ref, watch,
} from 'vue';

export default defineComponent({
  inheritAttrs: false,
  props: {
    modelValue: String,
    label: {
      type: String,
    },
    error: {
      type: String,
    },
    invalid: Boolean,
  },
  emits: ['update:modelValue'],
  setup(props) {
    const invalid = ref(props.invalid);
    const classes = computed(() => ({
      'input--error': invalid.value,
    }));

    watch(() => props.invalid, (newValue) => {
      invalid.value = newValue;
    });

    return {
      classes,
    };
  },
});
