<template>
    <div class="card" :class="classes">
      <h3 class="card__title"><slot name="title"/></h3>
      <template v-if="loading">
          <div class="card__overlay"><spinner-icon /></div>
      </template>
      <div class="card__body">
        <slot name="body"/>
      </div>
    </div>
</template>

<script>
import { defineComponent } from 'vue';
import SpinnerIcon from './spinner/SpinnerIcon.vue';

export default defineComponent({
  components: { SpinnerIcon },
  props: {
    isLoading: {
      type: Boolean,
      default: false,
    },
    flat: {
      type: Boolean,
      default: false,
    },
    fill: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    return {
      loading: props.isLoading,
      classes: {
        'card--flat': props.flat,
        'card--fill': props.fill,
      },
    };
  },
});
</script>

<style lang="scss">
.card {
    @apply shadow rounded-md border border-gray-200 relative;

    &--flat {
      @apply shadow-none border-transparent rounded-none;
    }

    &--fill > {
      .card {
        &__body {
          @apply px-0;
        }

        &__title {
          @apply px-0;
        }
      }
    }

    &__title {
      @apply font-semibold text-2xl pt-6 px-4 pb-2 border-b border-gray-200;

      &:empty {
        @apply p-0 m-0 border-none;
      }
    }

    &__body {
      @apply px-4 py-4;
    }

    &__overlay {
      @apply z-10 absolute;
      width: 100%;
      height: 100%;
    }
}
</style>
