import { ActionTree, MutationTree } from 'vuex';
import {
  AppError, Day, ErrorCode, ListDayType, Status, ToDoItem, ToDoList,
} from '@/types/shared';
import { RootState } from './types';
import ToDoService from '@/services/todoService';
import HabitService from '@/services/habitService';

const service = new ToDoService();
const habitService = new HabitService();

const todoState = () => ({
  [ListDayType.DailyHabit]: null as ToDoList | null,
  [ListDayType.Tomorrow]: null as ToDoList | null,
  [ListDayType.Today]: null as ToDoList | null,
  archives: [] as ToDoList[],
});

export type TodoState = ReturnType<typeof todoState>;

export const mutations: MutationTree<TodoState> = {
  UPDATE(state, payload: {listType: ListDayType, result: unknown}) {
    const result = payload.result as ToDoList;
    state[payload.listType] = result;
  },
};

export const actions: ActionTree<TodoState, RootState> = {
  async fetch({ commit }, payload: ListDayType): Promise<AppError | boolean> {
    const result = await service.getTodos(payload);

    if (result instanceof AppError) {
      if (result.code === ErrorCode.NotFound) {
        commit('UPDATE', {
          listType: payload,
          result: {},
        });
      }

      return result;
    }

    commit('UPDATE', {
      listType: payload,
      result,
    });

    return true;
  },
  async createList({ commit }, payload: {listType: ListDayType, list: ToDoList}) {
    const result = await service.createList(payload.listType, payload.list);

    if (result instanceof AppError) return result;

    commit('UPDATE', {
      listType: payload.listType,
      result,
    });
    return true;
  },
  async createHabitList({ commit }, payload: Day) {
    const result = await habitService.createDailyHabitTodos(payload);

    if (result instanceof AppError) return result;

    commit('UPDATE', {
      listType: payload,
      result,
    });
    return true;
  },
  async saveList({ commit }, payload: {listType: ListDayType, save: boolean}) {
    const result = await service.saveList(payload.listType, payload.save);

    if (result instanceof AppError) return result;

    commit('UPDATE', {
      listType: payload.listType,
      result,
    });
    return true;
  },
  async deleteList({ commit }, payload: {listType: ListDayType, force: boolean}) {
    const result = await service.deleteTodoList(payload.listType, payload.force);

    if (result instanceof AppError) return result;
    commit('UPDATE', {
      listType: payload.listType,
      result: {},
    });
    return true;
  },
  // eslint-disable-next-line no-empty-pattern
  async add({ }, payload: {listType: ListDayType, item: ToDoItem}) {
    const result = await service.addTodo(payload.listType, payload.item);

    if (result instanceof AppError) return result;

    return true;
  },
  // eslint-disable-next-line no-empty-pattern
  async delete({ }, payload: {listType: ListDayType, ids: (string | number)[]}) {
    const result = await service.deleteTodos(payload.listType, payload.ids);

    if (result instanceof AppError) return result;

    return true;
  },
  // eslint-disable-next-line no-empty-pattern
  async update({}, payload: {listType: ListDayType, item: ToDoItem}) {
    const result = await service.updateTodo(payload.listType, payload.item);

    if (result instanceof AppError) return result;

    return true;
  },
  // eslint-disable-next-line no-empty-pattern
  async updateStatus({ }, payload: {id: string | number, status: Status}) {
    const result = await service.updateTodoStatus(payload.status, payload.id);

    if (result instanceof AppError) return result;

    return true;
  },

  // eslint-disable-next-line no-empty-pattern
  async check({ }, payload: ListDayType) {
    const result = await service.hasTodoList(payload);

    if (result instanceof AppError) return result;

    return result;
  },
};

export default {
  namespaced: true,
  state: todoState,
  mutations,
  actions,
};
