import { cloneDeep } from 'lodash';
import { Ref, SetupContext } from 'vue';
import { Store } from 'vuex';
import { Router } from 'vue-router';
import {
  AppError, ListDayType, ToDoItem, ToDoItemExtension, ToDoList,
} from '@/types/shared';
import { CreateMode, overviewRoute } from '@/services/utils/link';

type PropTypes = {
  listType: ListDayType,
  mode: CreateMode,
  onlyView: boolean,
  todoList: ToDoList,
}

interface CreateActions {
  save(todos: Ref<ToDoItemExtension[]>): void,
  onArchive(): void,
  cancel(): void,
  onDeleteList(deleteList: Ref<boolean>): void,
}

export default (
  props: PropTypes,
  ctx: SetupContext<Record<string, unknown>>,
  store: Store<unknown>,
  router: Router,
): CreateActions => ({
  save(todos: Ref<ToDoItemExtension[]>) {
    const list = { ...props.todoList };
    list.todos = cloneDeep(todos.value.map((todo) => ({
      key: todo.key,
      description: todo.description,
      optional: todo.optional,
      status: todo.status,
      startTime: todo.startTime,
      endTime: todo.endTime,
      days: todo.days,
      carryOver: todo.carryOver,
      habit: false,
    } as ToDoItem)));
    ctx.emit('save', list);
  },
  async onArchive() {
    const result = await store.dispatch('todos/saveList', {
      listType: props.listType,
      save: !props.todoList.save,
    });

    if (result instanceof AppError) {
      console.log(result);
      return;
    }

    ctx.emit('refresh');
  },
  cancel() {
    router.push({
      path: overviewRoute.link,
    });
  },
  async onDeleteList(deleteList: Ref<boolean>) {
    const result = await store.dispatch('todos/deleteList', {
      listType: props.listType,
      force: true,
    });

    if (result instanceof AppError) {
      console.log(result);
      return;
    }

    // eslint-disable-next-line no-param-reassign
    deleteList.value = false;
    ctx.emit('refresh');
  },
});
