<template>
  <div class="todo-list">
    <alert v-if="error" variant="error">An error occurred</alert>
    <button-group v-if="!onlyView">
      <app-button
        @click="showAddItemComponent = true"
        @keydown="showAddItemComponent = true"
      >
        {{t('todos.addItemBtn')}}
      </app-button>
      <app-button
        link
        @click="cancel"
        @keydown="cancel"
      >
        <template v-if="isEditMode">
          {{t('todos.backBtn')}}
        </template>
        <template v-else>
          {{t('todos.cancelBtn')}}
        </template>
      </app-button>
      <app-button
        v-if="!isEditMode"
        @click="save"
        @keydown="save"
        variant="primary"
        :disabled="!hasTodos"
      >
        {{t('todos.saveBtn')}}
      </app-button>
    </button-group>
    <list>
      <list-item is-title>
        <span>{{dateTitle}}</span>
        <div class="todo-list__menu">
          <tool-tip v-if="onlyView">
            <template #element>
              <icon-button @click="onArchive" :selected="todoList.save">
                <img src="@/assets/images/icons/archive.svg" alt="archive">
              </icon-button>
            </template>
            <template #text>
              {{t('todos.archive')}}
            </template>
          </tool-tip>
          <div class="todo-list__options" v-if="onlyView">
            <dropdown :items="options" position="right" @selected="onSelectOption" />
          </div>
          <div class="todo-list__options" v-if="!onlyView && todos.length > 0">
            <checkbox v-model="checkAll" />
          </div>
        </div>
      </list-item>
      <list-item v-if="anyChecked">
        <icon-button @click="onDeleteItems">
        <img src="@/assets/images/icons/delete.svg" alt="delete">
      </icon-button>
      </list-item>
      <list-item v-if="todos.length === 0">
        {{t('todos.empty')}}
      </list-item>
      <list-item v-else v-for="(item, index) in todos" :key="item.key">
        <todo-item
          :todo="item"
          @setChecked="checkedItem"
          @itemSaved="itemSaved"
          @statusUpdate="statusUpdate"
          :checked="item.checked"
          :index="index"
          :isEditMode="!onlyView"
          :isForToday="isForToday"
          :listType="listType"
        >
          {{item.description}}
        </todo-item>
      </list-item>
    </list>
    <modal v-if="deleteList" @close="deleteList = false">
      <template #title>{{t('todos.deleteList.title')}}</template>
      <template #body>
        {{t('todos.deleteList.description')}}
        <i18n-t
          tag="span"
          keypath="todos.deleteList.description2"
        >
        <template v-slot:saved>
          <strong>{{ t('todos.deleteList.saved') }}</strong>
        </template>
        </i18n-t>
      </template>
      <template #footer>
        <button-group>
          <app-button
            @click="onDeleteList"
            variant="primary"
          >
            {{t('todos.deleteList.deleteBtn')}}
          </app-button>
        </button-group>
      </template>
    </modal>
    <add-item
      v-if="showAddItemComponent"
      @close="showAddItemComponent = false"
      @addItem="addItem"
      :listType="listType"
      mode="create"
    />
  </div>
</template>
<script lang="ts">
import {
  defineComponent, PropType, ref, watch,
} from 'vue';
import { useI18n } from 'vue-i18n';
import { useStore } from 'vuex';
import {
  AppError,
  ListDayType, Status, ToDoItemExtension, ToDoItemForm, ToDoList,
} from '@/types/shared';
import List from '@/components/list/List.vue';
import ListItem from '@/components/list/ListItem.vue';
import TodoItem from '@/components/todo/TodoItem.vue';
import Checkbox from '@/components/forms/Checkbox.vue';
import Modal from '@/components/Modal.vue';
import Dropdown from '@/components/Dropdown.vue';
import ButtonGroup from '@/components/ButtonGroup.vue';
import AppButton from '@/components/buttons/AppButton.vue';
import IconButton from '@/components/IconButton.vue';
import ToolTip from '@/components/ToolTip.vue';
import AddItem from '@/components/AddItem/AddItem.vue';
import { CreateMode, createRoute } from '@/services/utils/link';
import { DropdownItem } from '@/types/components';
import router from '@/router';
import createActions from './features/actions';
import { getLocalDate } from '@/services/utils/time';
import { createToDoItemReq } from '../AddItem/features/helpers';
import Alert from '@/components/Alert.vue';

const OPTIONS = {
  Edit: 'Edit',
  DeleteAll: 'DeleteAll',
};

const todoItemsComponent = defineComponent({
  components: {
    List,
    ListItem,
    TodoItem,
    Checkbox,
    Modal,
    ButtonGroup,
    AppButton,
    AddItem,
    Dropdown,
    IconButton,
    ToolTip,
    Alert,
  },
  props: {
    listType: {
      type: String as PropType<ListDayType>,
      required: true,
    },
    mode: {
      type: String as PropType<CreateMode>,
      default: 'create',
    },
    onlyView: {
      type: Boolean,
      default: true,
    },
    todoList: {
      type: Object as PropType<ToDoList>,
      required: true,
    },
  },
  setup(props, ctx) {
    const { t } = useI18n();
    const store = useStore();
    const checkAll = ref(false);
    const deleteList = ref(false);
    const anyChecked = ref(false);
    const hasTodos = ref(false);
    const showAddItemComponent = ref(false);
    const isEditMode = props.mode === 'edit';
    const isForToday = props.listType === ListDayType.Today;
    const dateTitle = getLocalDate(props.todoList.startDate || 0) || t('todos.title');
    const todos = ref<ToDoItemExtension[]>(props.todoList.todos);
    const optionsDefault: DropdownItem[] = Object.values(OPTIONS).map((anOption) => ({
      value: anOption,
      name: t(`todos.options.${anOption}`),
      disabled: false,
    }));
    const error = ref(false);
    const options = ref(optionsDefault);
    const actions = createActions(props, ctx, store, router);

    const onSelectOption = (params: {value: string, index: number}) => {
      if (params.value === OPTIONS.DeleteAll) {
        deleteList.value = true;
        return;
      }

      if (params.value === OPTIONS.Edit) {
        router.push({
          path: createRoute.link,
          query: {
            mode: 'edit' as CreateMode,
            type: props.listType,
          },
        });
      }
    };

    watch(() => props.todoList, (newValue) => {
      todos.value = newValue.todos;
    }, { deep: true });

    watch(checkAll, (newValue) => {
      if (newValue) {
        todos.value = todos.value.map((item) => ({ ...item, ...{ checked: true } }));
        return;
      }

      if (!todos.value.find((todo) => !todo.checked)) {
        todos.value = todos.value.map((item) => ({ ...item, ...{ checked: false } }));
      }
    });

    watch(todos, (newValue) => {
      hasTodos.value = newValue.length > 0;
      anyChecked.value = !!newValue.find((todo) => todo.checked);
    }, { deep: true });

    const checkedItem = (params: {checked: boolean, index: number}) => {
      todos.value[params.index].checked = params.checked;

      if (!params.checked) {
        checkAll.value = false;
      }
    };

    const addItem = async (item: ToDoItemForm) => {
      error.value = false;
      if (isEditMode) {
        const requestData = {
          listType: props.listType,
          item: createToDoItemReq(item),
        };
        const result = await store.dispatch('todos/add', requestData);
        if (result instanceof AppError) {
          error.value = true;
        } else {
          ctx.emit('refresh');
        }
        return;
      }

      todos.value.push(item);
    };

    const itemSaved = () => {
      ctx.emit('refresh');
    };

    const onDeleteItems = async () => {
      error.value = false;
      const toDelete = todos.value.filter((item) => item.checked).map((item) => item.id);

      if (isEditMode) {
        const result = await store.dispatch('todos/delete', {
          listType: props.listType,
          ids: toDelete,
        });

        if (result instanceof AppError) {
          error.value = true;
          console.log(result);
          return;
        }

        ctx.emit('refresh');
        return;
      }

      todos.value = todos.value.filter((todo) => !(Object.values(toDelete)).includes(todo.id));
    };

    const statusUpdate = async (params: {id: string | number, status: Status}) => {
      const result = await store.dispatch('todos/updateStatus', {
        id: params.id,
        status: params.status,
      });

      if (result instanceof AppError) {
        console.log(result);
        return;
      }

      ctx.emit('refresh');
    };

    return {
      t,
      error,
      dateTitle,
      hasTodos,
      isEditMode,
      options,
      todos,
      checkedItem,
      checkAll,
      deleteList,
      anyChecked,
      addItem,
      showAddItemComponent,
      itemSaved,
      onSelectOption,
      save: actions.save.bind(this, todos),
      cancel: actions.cancel,
      onDeleteItems,
      statusUpdate,
      onDeleteList: actions.onDeleteList.bind(this, deleteList),
      isForToday,
      onArchive: actions.onArchive,
    };
  },
});

export type TodoItemsComponent = typeof todoItemsComponent;
export default todoItemsComponent;
</script>

<style lang="scss" scoped>
@import "./TodoItems.scss";
</style>
