<template>
  <h1 data-id="page-title" class="font-black text-3xl capitalize" :class="titleClass"><slot /></h1>
  <h5 data-id="page-sub-title" class="text-lg text-gray-500" :class="subClass">
    <slot name="sub" />
  </h5>
</template>

<script lang="ts">
import { defineComponent } from 'vue';

export default defineComponent({
  setup(prop, ctx) {
    const hasSub = !!ctx.slots.sub;

    return {
      titleClass: {
        'mb-7': !hasSub,
      },
      subClass: {
        'mb-7': hasSub,
      },
    };
  },
});
</script>
